import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Cookies } from "react-cookie";
import './App.css';
import GetIcon from './modules/GetIcon';
import { GetChatLog } from './modules/DataRouter';
import { BackButton } from "./modules/Navigate";
const cookies = new Cookies();

export function AI() {
    const [message, setMessage] = useState('');
    const [chatLog, setChatLog] = useState([]);
    const [listening, setListening] = useState(false);
    const [typingMessage, setTypingMessage] = useState(' ');
    const typingIntervalRef = useRef(null);
    const recognitionRef = useRef(null);

    useEffect(() => {
        if (!('webkitSpeechRecognition' in window)) {
        alert('     .   .');
        }
    }, []);

    useEffect(() => {
        return () => {
            if (typingIntervalRef.current) {
                clearInterval(typingIntervalRef.current);
            }
            if (recognitionRef.current) {
                recognitionRef.current.stop();
            }
        };
    }, []);
    useEffect(() => {
        const chatLogElement = document.querySelector('.chat-log');
        chatLogElement.scrollTop = chatLogElement.scrollHeight;

        let didCancel = false;

        const fetchChatLog = async () => {
        try {
            const data = await GetChatLog();
            console.log(data);
            if (!didCancel) {
                for(let i = 0; i < data.length; i++){
                    updateChatLog({ sender: data[i].senderType === 'U' ? 'user' : 'bot', text: data[i].userChatContext });
                }
            }
        } catch (error) {
            if (!didCancel) {
                console.error('Failed to fetch chat log:', error);
            }
        }
        };

        fetchChatLog();

        return () => {
            didCancel = true;
        };
    }, []);
    const startListening = () => {
        if (!('webkitSpeechRecognition' in window)) return;
    
        const recognition = new window.webkitSpeechRecognition();
        recognitionRef.current = recognition;
        recognition.lang = 'ko-KR';
        recognition.continuous = false;
        recognition.interimResults = false;
    
        recognition.onstart = () => {
            setListening(true);
            startListeningIndicator();
        };
    
        recognition.onresult = (event) => {
            const transcript = event.results[0][0].transcript;
            setMessage(transcript);
            updateChatLog({ sender: 'user', text: transcript });
            getLocationAndSendMessage(transcript);
            setListening(false);
            stopListeningIndicator();
        };
    
        recognition.onerror = (event) => {
            console.error('  :', event.error);
            setListening(false);
            stopListeningIndicator();
        };
    
        recognition.onend = () => {
            setListening(false);
            stopListeningIndicator();
        };
    
        recognition.start();
    };
    const stopListening = () => {
        if (recognitionRef.current) {
          recognitionRef.current.stop();
          recognitionRef.current = null;
        }
      };
      const getLocationAndSendMessage = (transcript) => {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(
            (position) => {
              const { latitude, longitude } = position.coords;
              sendMessageWithLocation(transcript, latitude, longitude);
            },
            (error) => {
              console.error('     :', error);
              sendMessage(transcript, false);
            },
            { timeout: 10000, enableHighAccuracy: true } //    
          );
        } else {
          console.error('     .');
          sendMessage(transcript, false);
        }
      };

    const sendMessageWithLocation = async (msg, latitude, longitude) => {
        if (!msg.trim()) return;

        updateChatLog({ sender: 'user', text: msg });

        try {
        startTypingIndicator();

        const token = cookies.get("token");

        if (!token) {
            console.log(" ");
            return false; //    false 
        }

        const response = await axios.post('https://culiso.duckdns.org/chat', { message: msg, latitude, longitude }, {headers : {"Authorization": `Bearer ${token}`}});
        stopTypingIndicator();
        handleBotResponse(response.data.response);
        } catch (error) {
        stopTypingIndicator();
        console.error('       :', error);
        alert('       .');
        }
    };

    const sendMessage = async (msg, isVoice = false) => {
        if (!msg.trim()) return;
        if (!isVoice) {
            updateChatLog({ sender: 'user', text: msg });
        }

        try {
            startTypingIndicator();


            const token = cookies.get("token");

            if (!token) {
                console.log(" ");
                return false; //    false 
            }

        

            const response = await axios.post('https://culiso.duckdns.org/chat', { message: msg }, {headers : {"Authorization": `Bearer ${token}`}});
            stopTypingIndicator();
            handleBotResponse(response.data.response);
        } catch (error) {
            stopTypingIndicator();
            console.error('    :', error);
            alert('    .');
        }
    };

    const handleBotResponse = (response) => {
        const cleanedResponse = response.replace(/\*\*|###|####/g, ''); // '**' '###' 
        updateChatLog({ sender: 'bot', text: cleanedResponse });
    };

    const updateChatLog = (message) => {
        setChatLog((prevChatLog) => [...prevChatLog, message]);
        setMessage('');
    };

    const handleInputChange = (e) => {
        setMessage(e.target.value);
    };

    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            getLocationAndSendMessage(message);
        }
    };

    const formatMessage = (text) => {
        return text.split('\n').map((line, index) => {
        if (line.startsWith('  ')) { //    
            return <div key={index} className="formatted-line indented">{line}</div>;
        }
        return <div key={index} className="formatted-line">{line}</div>;
        });
    };

    const startTypingIndicator = () => {
        setTypingMessage(' ');
        typingIntervalRef.current = setInterval(() => {
        setTypingMessage(prev => prev.length < 9 ? prev + '.' : ' ');
        }, 500);

        updateChatLog({ sender: 'bot', text: ' ' });
    };

    const stopTypingIndicator = () => {
        if (typingIntervalRef.current) {
        clearInterval(typingIntervalRef.current);
        typingIntervalRef.current = null;
        }

        // Remove the typing indicator
        setChatLog((prevChatLog) => prevChatLog.filter((message, index) => index !== prevChatLog.length - 1));
    };
    const startListeningIndicator = () => {
        updateChatLog({ sender: 'bot', text: '  ' });
    };
    
    const stopListeningIndicator = () => {
        setChatLog((prevChatLog) => prevChatLog.filter((message) => message.text !== '  '));
    };
    return (
        <div className="ai">
            <div className='aiDiv'>
                <div className="App">
                    <div className='topMenubar'>
                        <BackButton left="20px"/>
                        <span style={{marginLeft: "10px"}}></span>
                    </div>
                    <div className="chat-log">
                        <div>
                            {chatLog.map((entry, index) => (
                            <div>
                                {entry.sender === 'bot' && 
                                <div className="culiBox">
                                    <img className="culi" src={GetIcon("chatbot-white7.png")} alt="bot" />
                                    <div><span></span></div>
                                </div>}
                                <div className={`chat-box ${entry.sender}`}>
                                    <div key={index} className={`chat-message ${entry.sender}`}>
                                        {entry.sender === 'bot' && entry.text === ' ' ? typingMessage : formatMessage(entry.text)}
                                    </div>
                                </div>
                            </div>
                            ))}
                        </div>
                    </div>
                    <div className='chat-box'>
                        <div className="chat-input">
                            <input
                                type="text"
                                value={message}
                                onChange={handleInputChange}
                                onKeyPress={handleKeyPress}
                                placeholder=" .."
                            />
                            <img src={GetIcon("send-blue.png")} style={{right: '0', marginRight: '60px'}} width="30px"onClick={()=> getLocationAndSendMessage(message)} alt=''/>
                            {listening ? (
                                alert(' ...')
                            ) : (
                                <img 
                                    src={GetIcon("voice-blue.png")} 
                                    style={{right: '0', marginRight: '15px'}} 
                                    width="30px"
                                    alt='' 
                                    onMouseDown={startListening} 
                                    onMouseUp={stopListening}
                                    onTouchStart={startListening} 
                                    onTouchEnd={stopListening}
                                    onTouchCancel={stopListening}
                                    />
                                )
                            }
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    );
}